@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@100;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
	margin: 0px;
	padding: 0px;
	box-sizing: border-box;
	font-family: 'Open Sans';
	font-weight: 200;
	font-size: 16px;
	line-height: 150%;
}

.section-content {
	margin-top: 5%;
	margin-bottom: 5%;
	margin-left: 8%;
	margin-right: 8%;
} 

.section-header {
	display: flex;
  	justify-content: center; 
  	align-items: center;
	height: 100px;
	background: white;
	margin-bottom: 48px;
	text-transform: uppercase;
	border-top: 1px solid #dfdfe5;
	border-bottom: 1px solid #dfdfe5;
}

.section-header p {
	text-transform: uppercase;
	font-weight:300;
	font-family: 'Libre Franklin';
	letter-spacing: 2px;
	font-size: 18px;
}

.page-content {
	margin-bottom: 64px;
	background: white;
}
.header-bar {
  display:flex;
  justify-content: space-between;
  /*min-height: 100px;*/
  /*background-color: #f0e4d8;*/
  /*background-color: #f9f9f9;*/
  background-color: white;
  align-items: center;
  padding: 0 24px;
  position:fixed;
  width: 100%;
  height: 100px;
  top:0;
}

/********************************/
/***  Title (left hand side)  ***/
/********************************/
.title {
  text-align: center;
}
.title > p {
  text-transform: uppercase;
  font-size: 20px;
  font-family: 'Libre Franklin';
  letter-spacing: 2px;
}

/************************/
/***  Nav links menu  ***/
/************************/
.nav-links {
	display: flex;
	justify-content: flex-end;
}
.nav-links li {
  list-style: none;
  padding: 2px 16px;
}

.nav-links a {
  text-decoration: none;
  color: inherit;
	letter-spacing: 3px;
  text-transform: uppercase;
  font-size: 14px;
  font-family: 'Libre Franklin';
}

.nav-links a:hover {
  font-weight: 500;
}

/*********************/
/***  BURGER MENU  ***/
/*********************/



/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  right: 36px;
  top: 36px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #373a47;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #e2e2db;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #373a47 ;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the 
animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
  top: 0px;
}

/* General sidebar styles */
.bm-menu {
  background: #f9f9f9;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #4b5556;
  text-transform: uppercase;
  padding: 0.8em;
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: white !important;
  opacity: 0 !important;
}
/*links in the burger menu*/
.bm-item-list a {
  padding: 12px;
  cursor: pointer;
  text-decoration: none;
  color: inherit;
}

  
.home-body {
    display: flex;
    justify-content: space-between;
    height: 878px;
    align-content: stretch;
}
.left-side {
    width: 50%;
    /*background: #f0e4d8;*/
    background: #e2e2db;
    display:flex;
    align-items: center;
    justify-content: center;
}
.left-side img {
    max-width: 60%;
    height:auto;
}
.right-side {
    width:50%;
    display: flex;
    flex-direction: column;
    justify-content: center; 
    flex-flow: column wrap;
}
.welcome-text {
    position: relative;
    padding: 0 64px 0 32px;
}
.welcome-text #hello {
    font-size: 100px;
    font-family: 'Libre Franklin';
    font-weight: 600;
}
.welcome-text #intro {
    font-size: 28px;
    font-weight: 300;
}
.external-links{
    padding: 24px 40px;
}
.external-links img {
    max-width: 80px;
    max-height: 80px;
    padding-right: 40px;
}



.project-row {
	display:flex;
	justify-content: center;
	text-align: center;
}

.project-item {
	display: inline-block;
	width: 400px;
	height: 400px;
	margin:20px;
	background-color: #f6f5f0;
	box-shadow:10px 10px 10px grey;
}
@media screen and (max-width: 880px) {
	.project-row {
		flex-direction: column;
		flex-flow: column wrap;
	}
}

/*Project Description*/
.project-item p {
	padding: 25px;
	padding-top:0px;
	text-align: left;
	background-color: white;
}

.project-item h3 {
	font-family: 'Open Sans';
	font-weight: bold;
	text-transform: uppercase;
	background-color: white;
	
}
.resume-page {
    background-color: #dfe4e6;
}

.resume-section-header h2{
    font-family: 'Libre Franklin';
    font-size: 18px;
    font-weight:300;
    text-transform: uppercase;
    letter-spacing: 2px;
    padding: 30px 0;
}
.resume-content p{
    font-size: 15px;
}
.resume-item {
    padding-top:24px;
    padding-bottom: 24px;
}
.resume-item-title {
    display:flex;
    justify-content: space-between;
}
.resume-item-title > .item-role-company > p {
    text-transform: uppercase;
    font-weight: 600; 
}
.resume-item-title > .item-date-location {
    padding-left:48px;
    text-align: right;
}

#coursework > h2 {
    padding-top: 24px;
    padding-bottom: 12px;;
    font-weight: 600;
}
.resume-description {
    padding-top: 8px;;
}
.resume-description > ul {
    margin-left: 24px;
}

.skills-container {
    display:flex;
    justify-content: flex-start;
    flex-wrap: wrap;
}
.skills-container div {
    font-size: 15px;
    margin: 5px;
    padding: 5px;
    border: 1px dashed blue;
    border-radius: 5px;
    font-family: 'Montserrat';
}

